.header {
  position: relative;
}

.logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  text-align: center;
}

.logo--nav-closed {
  display: none;
}

nav {
  min-height: 56px;
  background-color: #ffffff;
}

.nav__list {
  position: relative;
  margin: 0;
  padding: 0;
  min-height: 56px;
  background-color: #81b3d2;
}
@media (min-width: 767px) {
  .nav__list {
    position: static;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    background-color: #ffffff;
    box-shadow: none;
  }
}

@media (max-width: 767px) {
  .nav__list--closed {
    display: none;
  }
}

.nav__list::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  height: 7px;
  width: 100%;
  box-shadow: 0, 1px rgba(255, 255, 255, 0.2);
}

.nav__item {
  font-size: 18px;
  line-height: 25px;
  font-weight: 700;
  list-style: none;
  text-transform: uppercase;
  border-top: 1px solid #9ac2db;
  border-bottom: 1px solid #678fa8;
}
@media (min-width: 767px) {
  .nav__item {
    font-size: 14px;
    line-height: 21px;
    border: none;
    width: 20%;
  }
}

@media (min-width: 767px) {
  .nav__item--foto {
    margin-right: 20%;
  }
}

@media (max-width: 767px) {
  .nav__item:last-child {
    margin-bottom: 0;
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 100% 7px;
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
    border-bottom: none;
  }
}

.nav__link {
  display: flex;
  justify-content: center;
  color: #ffffff;
  text-decoration: none;
  padding: 17px 70px 12px;
}
@media (max-width: 767px) {
  .nav__link:hover {
    background-color: #669ec0;
  }
  .nav__link:active {
    background-color: #5496bd;
  }
  .nav__link:disabled {
    background-color: #f5f5f5;
  }
}
@media (min-width: 767px) {
  .nav__link {
    color: #000000;
    padding: 18px 0 17px;
  }
  .nav__link:hover {
    color: #766357;
  }
}

@media (max-width: 767px) {
  .nav__link span:active {
    opacity: 0.3;
  }
}

.nav__toggle {
  content: "";
  position: absolute;
  top: 17px;
  right: 20px;
  background-color: #ffffff;
  width: 23px;
  height: 22px;
  margin-bottom: 17px;
  background-repeat: no-repeat;
  border: none;
  z-index: 2;
  cursor: pointer;
}
.nav__toggle:hover::before {
  opacity: 0.6;
}
.nav__toggle:active::before {
  opacity: 0.3;
}
.nav__toggle:hover::after {
  opacity: 0.6;
}
.nav__toggle:active::after {
  opacity: 0.3;
}
@media (min-width: 767px) {
  .nav__toggle {
    display: none;
  }
}

.nav--closed .nav__toggle::before {
  content: "";
  position: absolute;
  background-image: url("../img/icon-nav.svg");
  width: 23px;
  height: 22px;
  top: 0;
  right: 0;
}

.nav--open .nav__toggle::after {
  content: "";
  position: absolute;
  background-image: url("../img/icon-close.svg");
  background-color: #81b3d2;
  z-index: 2;
  top: 0;
  right: 0;
  width: 100%;
  height: 22px;
}

.nav--nojs {
  position: static;
}

